export default {
	props: {
		item: Object,
	},
	data() {
		return {
			info:{}
		};
	},
	mounted() {
		this.info = Object.assign({},this.item);
	},
	methods: {
		click(id,type) {
			this.$emit('click',id)
		},
		//监听image加载完成
		onImageLoad() {
			this.$set(this.info, 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError() {
			this.info.pic = '/static/healthy-mall/errorImage.jpg';
		},
	},
	filters: {
		toFixed2: function(value) {
			return Number(value).toFixed(2);
		},
	}
}
